* {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: rgb(1, 10, 10);
}

img {
  cursor: pointer;
}
.container2 {
  display: flex;
  justify-content: center;
  align-items: center;
  /* margin-top: 21rem; */
  position: absolute;
  top: 20rem;
  left: 56rem;
}

.flex-div {
  display: flex;
  align-items: center;
}

nav {
  justify-content: space-between;
  position: sticky;
  top: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 10;
  background-color: #fff;

}

.nav-right img {
  width: 25px;
  margin-right: 25px;
}

.nav-left .menu {
  width: 25px;
  margin: 25px;
}

.nav-left .logo {
  width: 40px;

}

.nav-center .search {
  border: 1px solid black;
  margin: 2px 15px;
  border-radius: 20px;
  padding: 7px 12px;
}

.nav-center .search input {
  width: 400px;
  border: 0;
  outline: 0;
  background: transparent;
}

.sidebar {
  background: #fff;
  width: 15%;
  height: 100vh;
  position: fixed;
  top: 0;
  padding: 1%;
  padding-top: 80px;
}

.small_sidebar{
  width: 5%;
}
.small_sidebar a p{
  display: none;
}
.links a img {
  width: 2rem;
  margin: 5px;
}


.links a {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  width: fit-content;
  flex-wrap: wrap;
}

.container1 {
  background: #fff;
  padding: 16%;
  padding-right: 2%;
  padding-top: 10px;
  padding-bottom: 20px;
}

.big_container {
  padding: 2%;
  padding-right: 1%;
  padding-top: 5px;
  padding-bottom: 4px;

}
.big_container .cate img {
  width: 24rem;
  height: 22rem;
  margin: 1rem;
  border-radius: 1.5rem;
}

.big_container .cate {
  margin-left: 3rem;
  width: 26rem;
}

#categories {
  display: grid;
  /* grid-template-columns: 2fr 2fr 2fr 2fr; */
  grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
  grid-gap: 0.5rem;
  margin: 0px 1px;


}

