.cate {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: center;
  border: 2px solid;
  color: white;
  border-radius: 10px;
  /* padding: 26px 95px; */
  /* margin:55px 22px; */
  margin-top: 30px;
  font-size: 1rem;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  cursor: pointer;
  transition: all 0.25s ease-in-out 0s;
  background-color: linear-gradient(52deg, #5c6c80 0%, #5c6c80 70%);
  width: 30rem;
}

.cate img {
  width: 28rem;
  height: 20rem;
  margin: 1rem;
  border-radius: 1.5rem;
}

.cate-info {
  display: flex;
  align-items: flex-start;
  margin: 0.5rem;
}
.cate-creator{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.cate-creator p{
  margin-bottom: 2px;
}

@media(max-width: 450px) {

  .menu{
    display: none;
  }
  .logo{
    margin-left: 1rem;
  }
  .sidebar{
    display: none;
  }
  .nav-right img{
    display: none;
  }
  .nav-right .user-icon{
    display: block;
    width: 1.5srem;
  }
  .nav-center .search{
    margin: 15px;
  }
  .nav-center .search input{
    width: 10rem;
  }
  .cate img{
    width: 18rem;
    height: 15rem;
  }
  .cate{
    width: 20rem;

  }
  .container1{
    padding: 7%;
  }
  nav{
  justify-content: space-between;
  position: sticky;
  top: 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  z-index: 10;
  background-color: #fff;
  width: -webkit-fill-available;
}
}
